import React from 'react';

export default function MissionKey() {
  return (
    <div className="my-3">
     <div className="row"> <p className="col-3">
        <span className="px-3 mr-2 bg-success" /> = Success
      </p>
      <p className="col-3">
        <span className="px-3 mr-2 bg-danger" /> = Fail
      </p></div>
    </div>
  );
}
